.notify {
    position: fixed;
    width: 500px;
    top: 10vh;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 12px;
    padding: 15px 45px 15px 25px;

    & ul {
        list-style: none;
    }
}

.notify-close {
    position: absolute;
    right: 0;
    top: 0;
    margin-right: 15px;
    margin-top: 0px;
    font-size: 2.4rem;
    cursor: pointer;
}